#calendar {
  width: 350px;
  background-image: linear-gradient(to bottom, #4e74ff, #4e74ff);
  margin: 0 auto;
  font-family: 'Open Sans',sans-serif;
  text-align: center;
  color: #555;
  box-shadow: 0 0 50px -14px rgba(0, 0, 0, 0.8);
  position: relative;
  z-index: 9;
  opacity: 92%;
  border: #42444d solid;
  border-radius: 10px;
  margin-top: 148px;
  background: #42444d; }
  #calendar table {
    border-spacing: 0; }
    #calendar table td {
      width: 38px;
      height: 38px;
      background: #42444d;
      padding-top: 21px;
      padding-left: 0px;
      cursor: pointer;
      transition: background .3s;
      -webkit-transition: background .3s;
      color: #4e74ff; }
    #calendar table .calendar-mn {
      padding-left: 12px; }
    #calendar table .calendar-sn {
      padding-right: 26px;
      padding-left: 4px; }
    #calendar table .lastmonth {
      background: #42444d;
      color: #42444d;
      cursor: default; }
    #calendar table .nextmonth {
      background: #42444d;
      color: #42444d;
      cursor: default; }
      #calendar table .nextmonth ~ * {
        background: #42444d;
        color: #42444d;
        cursor: default; }
    #calendar table .current {
      font-weight: 700;
      color: #fff;
      text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
      padding-bottom: 25px;
      padding-left: 24px; }
  #calendar .hastask {
    font-weight: 700; }
  #calendar tr:last-of-type td:first-of-type {
    border-radius: 0 0 0 0px; }
  #calendar tr:last-of-type td:last-of-type {
    border-radius: 0 0 0px 0; }

